.table {
    display: grid;
    grid-template-rows: repeat(auto, 1fr);
    margin: 15px 0;
}

.row {
    display: grid;
    grid-template-columns: 1fr 4fr 4fr;
    color: #474955 
}

.table p {
    grid-column: 1/2;
    grid-row: 5/6;
    justify-self: center;
    align-self: center;
}